.newpreview {
  background-color: #000a29;
  height: auto;
  min-height: 100vh;
  overflow-x :hidden;
  
}
.newpreview .navbar-brand {
color: yellow;
font-weight: bold;
font-family:'Times New Roman', Times, serif;
font-size: x-large;
}

.newpreview button {
margin: 0 1vh;

padding: 4px;
} 

.dataframeview {
  /* overflow-x: scroll;
  overflow-y: scroll; */
 
 
  width: 100%;
  
}



.left {
padding-left: 3vh;
padding-top: 1vh;


}

.newpreview .nav-item .uploadButton{

background-color: #f50057;
margin: 20px 20px 20px 0px;
width: 10vh;
margin-left: -2vh;
padding: 5px;
border-radius: 20px;
box-shadow: 5px 10px rgba(223, 223, 223, 0.15);
}
.newpreview .nav-item .disableButton {
    
background-color: #000000;
background-image: linear-gradient(315deg, #000000 0%, #7f8c8d 74%);


align-self: center;



margin: 20px 20px 20px 0px;
width: 10vh;
margin-left: -2vh;
padding: 5px;
border-radius: 20px;
box-shadow: 5px 10px rgba(255,255,255,0.15);
}

