.newpreview .navbar-nav  .nav-link {
text-align: center;
color: white;
font-weight: 500;
font-size: small;
}

.newpreview .navbar-nav  .nav-link:hover {
font-weight: 500;
color: rgb(0, 0, 0);

}

.newpreview .navbar-nav  .nav-link:link {
  text-decoration: none;
  color: white;
}

.newpreview .navbar-nav  .nav-link:visited {
  text-decoration: none;
  color: white;
}



.newpreview .navbar-nav  .nav-link:active {
  text-decoration: none;
  color: white;
}


.newpreview .fas {
color: rgb(255, 255, 255);
margin: 1vh 1vh 1vh 2vh;
}

.newpreview .box {
background-color: #1d1e33;
margin: 50px 10px 20px 0px;
width: 7vh;
margin-left: 3vh;
padding: 5px;
border-radius: 10px;
box-shadow: 5px 10px rgba(255,255,255,0.15);
height : 100%
}

.newpreview .nav-item .downloadButton {
background-color: #045de9;
background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
align-self: center;
text-align: center;


margin: 80px 10px 20px 10px;
width: 12vh;
margin-left: 5%;

padding: 8px;
border-radius: 40px;
box-shadow: 5px 10px rgba(255,255,255,0.15);
}

.newpreview .progressbar {
margin-top: 5vh;

margin-right: 10px;
}

